	data() {
		return {
			editType: 'edit',
			id: '',
			name: '',
			tel: '',
			isDefault: true,
			region: { address: '请点击选择地址' },
			label: null
		};
	},
	methods: {
		radioChange: function(evt) {
			this.label = evt.detail.value;
			// console.log(evt.detail.value);
			// console.log(this.label);
			// console.log(evt.detail.value === this.label);
		},
		chooseCity() {
			uni.chooseLocation({
				success: res => {
					// console.log(res);
					this.region = res;
				}
			});
		},
		isDefaultChange(e) {
			this.isDefault = e.detail.value;
		},
		del() {
			uni.showModal({
				title: '提示',
				content: '确认删除此地址？',
				success: res => {
					if (res.confirm) {
						uni.request({
							url: this.$tempUrl + 'userAddress/delete',
							method: 'DELETE',
							data: { id: this.id, openId: uni.getStorageSync('openId') },
							success: res => {
								if (res.data.code === 0) {
									uni.getStorage({
										key: 'confirmAddr',
										success: ret => {
											if (ret.data.id == this.id) {
												uni.removeStorage({
													key: 'confirmAddr',
													success: () => {
														uni.navigateBack({ delta: 1 });
													}
												});
											} else {
												uni.navigateBack({ delta: 1 });
											}
										}
									});
								}
							}
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		save() {
			let data = {
				userWechatOpenid: uni.getStorageSync('openId'),
				contactName: this.name,
				contactPhone: this.tel,
				areaName: this.region.address,
				address: this.region.name,
				latitude: this.region.latitude,
				longitude: this.region.longitude,
				label: this.label
				// isDefault: this.isDefault
			};
			if (this.editType == 'edit') {
				data.id = this.id;
			}
			if (!data.contactName) {
				uni.showToast({ title: '请输入收件人姓名', icon: 'none' });
				return;
			}
			if (!data.contactPhone) {
				uni.showToast({ title: '请输入收件人电话号码', icon: 'none' });
				return;
			}
			if (!data.areaName == '请点击选择地址') {
				uni.showToast({ title: '请选择收件地址', icon: 'none' });
				return;
			}
			if (!data.address) {
				uni.showToast({ title: '请输入收件人详细地址', icon: 'none' });
				return;
			}
			// uni.showLoading({
			// 	title: '正在提交'
			// });
			//实际应用中请提交ajax,模板定时器模拟提交效果
			if (this.editType == 'edit') {
				uni.request({
					url: this.$tempUrl + 'userAddress/updateForLatitudeAndLongitude',
					method: 'PUT',
					data: data,
					success: res => {
						if (res.data.code === 0) {
							uni.setStorage({
								key: 'confirmAddr',
								data: data,
								success: () => {
									// uni.hideLoading();
									uni.showToast({ title: '编辑地址成功', icon: 'none' });
									setTimeout(() => {
										uni.navigateBack({ delta: 2 });
									}, 200);
								}
							});
						} else {
							uni.showToast({ title: '失败', icon: 'none' });
						}
					}
				});
			} else {
				uni.request({
					url: this.$tempUrl + 'userAddress/insertForLatitudeAndLongitude',
					method: 'POST',
					data: data,
					success: res => {
						if (res.data.code === 0) {
							uni.setStorage({
								key: 'confirmAddr',
								data: data,
								success: () => {
									// uni.hideLoading();
									uni.showToast({ title: '新建地址成功', icon: 'none' });
									setTimeout(() => {
										uni.navigateBack({ delta: 2 });
									}, 200);
								}
							});
						} else {
							uni.showToast({ title: '失败', icon: 'none' });
						}
					}
				});
			}
		}
	},
	onLoad(e) {
		//获取传递过来的参数
		this.editType = e.type;
		if (e.type == 'edit') {
			uni.setNavigationBarTitle({
				title: '编辑收货地址'
			});
			// uni.getStorage({
			// 	key: 'address',
			// 	success: e => {
			this.id = e.id;
			this.name = e.contactName;
			this.tel = e.contactPhone;
			this.region.address = e.areaName;
			this.region.name = e.address;
			this.region.latitude = e.latitude;
			this.region.longitude = e.longitude;
			console.log(e.label);
			this.label = e.label;
			// this.isDefault = e.isDefault;
			// 	}
			// });
		}
	}
};
